/**
 * Image optimization utilities to improve mobile performance
 */

/**
 * Lazy loads images with IntersectionObserver
 * @param {string} selector - CSS selector for images to lazy load
 */
export function setupLazyLoading(selector = '.lazy-image') {
  try {
    if (typeof window === 'undefined' || typeof document === 'undefined') return;
    
    if ('IntersectionObserver' in window) {
      const lazyImageObserver = new IntersectionObserver((entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const lazyImage = entry.target;
            if (lazyImage.dataset.src) {
              const img = new Image();
              img.onload = function() {
                lazyImage.src = lazyImage.dataset.src;
                lazyImage.removeAttribute('data-src');
                lazyImage.classList.remove('lazy-image');
                lazyImage.classList.add('loaded');
              };
              img.src = lazyImage.dataset.src;
            }
            if (lazyImage.dataset.srcset) {
              lazyImage.srcset = lazyImage.dataset.srcset;
              lazyImage.removeAttribute('data-srcset');
            }
            lazyImageObserver.unobserve(lazyImage);
          }
        });
      }, {
        rootMargin: '100px'
      });

      const lazyImages = document.querySelectorAll(selector);
      lazyImages.forEach((lazyImage) => {
        lazyImageObserver.observe(lazyImage);
      });
    } else {
      // Fallback for browsers without IntersectionObserver
      const lazyImages = document.querySelectorAll(selector);
      lazyImages.forEach((lazyImage) => {
        if (lazyImage.dataset.src) {
          lazyImage.src = lazyImage.dataset.src;
          lazyImage.removeAttribute('data-src');
        }
        if (lazyImage.dataset.srcset) {
          lazyImage.srcset = lazyImage.dataset.srcset;
          lazyImage.removeAttribute('data-srcset');
        }
        lazyImage.classList.remove('lazy-image');
        lazyImage.classList.add('loaded');
      });
    }
  } catch (e) {
    console.error("Error in setupLazyLoading:", e);
  }
}

/**
 * Returns an image URL with responsive width based on device size
 * @param {string} imageUrl - Original image URL
 * @param {number} width - Target width for the image
 * @returns {string} Optimized image URL
 */
export function getResponsiveImageUrl(imageUrl, width = 800) {
  // If using a CDN like Cloudinary, Imgix, etc., you can construct responsive URLs
  // For local images without a CDN, we simply return the original URL
  try {
    // Don't break if window is undefined (SSR)
    if (typeof window !== 'undefined') {
      const isMobile = window.innerWidth <= 768;
      const isTablet = window.innerWidth > 768 && window.innerWidth <= 1024;
      
      // If using a CDN service, you could modify the URL here
      // Example with a hypothetical CDN:
      // if (imageUrl.includes('cdn.example.com') && (isMobile || isTablet)) {
      //   const adjustedWidth = isMobile ? Math.min(width, 480) : Math.min(width, 768);
      //   return imageUrl.replace(/\/w\d+\//, `/w${adjustedWidth}/`);
      // }
    }
  } catch (e) {
    console.error("Error in getResponsiveImageUrl:", e);
  }
  
  // Return original URL as fallback
  return imageUrl;
}

/**
 * Generates srcset attribute values for responsive images
 * @param {string} baseUrl - Base image URL without extension
 * @param {string} extension - Image extension (jpg, png, etc.)
 * @returns {string} Formatted srcset attribute value
 */
export function generateSrcSet(baseUrl, extension = 'jpg') {
  // Example: for an image like "image.jpg", baseUrl would be "image"
  const widths = [320, 480, 768, 1024, 1440];
  
  // Create srcset with various widths
  // For actual implementation, you would need images at these sizes
  // or a CDN that can dynamically resize images
  return widths
    .map(width => `${baseUrl}-${width}.${extension} ${width}w`)
    .join(', ');
}

/**
 * Create a blurred placeholder for images
 * @param {string} imageUrl - URL of the image to generate placeholder for
 * @param {number} width - Width of placeholder
 * @param {number} height - Height of placeholder
 * @returns {Promise<string>} Data URL of the placeholder
 */
export function createImagePlaceholder(imageUrl, width = 20, height = 20) {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.crossOrigin = 'Anonymous';
    img.onload = () => {
      const canvas = document.createElement('canvas');
      canvas.width = width;
      canvas.height = height;
      const ctx = canvas.getContext('2d');
      
      // Draw small image first
      ctx.drawImage(img, 0, 0, width, height);
      
      // Get data URL
      const placeholderUrl = canvas.toDataURL('image/jpeg', 0.5);
      resolve(placeholderUrl);
    };
    img.onerror = reject;
    img.src = imageUrl;
  });
}

/**
 * Preload critical images
 * @param {Array<string>} imageUrls - Array of image URLs to preload
 */
export function preloadCriticalImages(imageUrls) {
  if (!imageUrls || !imageUrls.length) return;
  
  const preloadLink = document.createElement('link');
  preloadLink.rel = 'preload';
  preloadLink.as = 'image';
  
  imageUrls.forEach((url) => {
    const link = preloadLink.cloneNode();
    link.href = url;
    document.head.appendChild(link);
  });
}

/**
 * Adds loading="lazy" attribute to images
 * @param {string} selector - CSS selector for images
 */
export function addNativeLazyLoading(selector = 'img:not([loading])') {
  if ('loading' in HTMLImageElement.prototype) {
    const images = document.querySelectorAll(selector);
    images.forEach(img => {
      img.setAttribute('loading', 'lazy');
    });
  }
}

/**
 * Initialize all image optimizations
 */
export function initImageOptimizations() {
  // Add native lazy loading attribute where supported
  if (document.readyState === 'complete') {
    addNativeLazyLoading();
  } else {
    window.addEventListener('load', () => {
      addNativeLazyLoading();
    });
  }
  
  // Set up custom lazy loading for older browsers
  setupLazyLoading();
}

export default {
  setupLazyLoading,
  getResponsiveImageUrl,
  generateSrcSet,
  createImagePlaceholder,
  preloadCriticalImages,
  addNativeLazyLoading,
  initImageOptimizations
}; 