import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import { theme } from './styles/theme';
import { GlobalStyles } from './styles/GlobalStyles';
import Header from './components/layout/Header';
import Footer from './components/layout/Footer';
import Home from './pages/Home';
import VideoEditing from './pages/VideoEditing';
import Designing from './pages/Designing';
import WebSupport from './pages/WebSupport';
import CreatorOutreach from './pages/CreatorOutreach';
import Careers from './pages/Careers';
import PrivacyPolicy from './pages/PrivacyPolicy';
import TermsOfService from './pages/TermsOfService';
import { initImageOptimizations } from './utils/imageOptimization';

// Font Awesome Icon Library
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import { 
  faPhone, 
  faEnvelope, 
  faMapMarkerAlt, 
  faPalette, 
  faGlobe, 
  faVideo, 
  faHandshake, 
  faBriefcase, 
  faArrowRight,
  faShieldAlt,
  faFileContract
} from '@fortawesome/free-solid-svg-icons';

// Initialize FontAwesome Library
library.add(
  fab, 
  faPhone, 
  faEnvelope, 
  faMapMarkerAlt, 
  faPalette, 
  faGlobe, 
  faVideo, 
  faHandshake, 
  faBriefcase, 
  faArrowRight,
  faShieldAlt,
  faFileContract
);

// Get base path from package.json homepage
const getBasePath = () => {
  // Default path for local development
  let basePath = '';
  
  // Check if we're in production
  if (process.env.NODE_ENV === 'production') {
    // Extract the path portion from the homepage in package.json
    // Example: If homepage is "https://yotproductions.github.io/official-website",
    // we want to extract "/official-website"
    const homepage = process.env.PUBLIC_URL || '';
    if (homepage) {
      try {
        // Use URL parsing if it's a full URL
        if (homepage.startsWith('http')) {
          const url = new URL(homepage);
          basePath = url.pathname;
        } else {
          // Otherwise just use the path directly
          basePath = homepage;
        }
        
        // Remove trailing slash if present
        if (basePath.endsWith('/')) {
          basePath = basePath.slice(0, -1);
        }
      } catch (e) {
        console.error('Error parsing homepage URL:', e);
      }
    }
  }
  
  return basePath;
};

// Scroll to top on navigation
function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

// Handle mobile viewport
const handleMobileViewport = () => {
  // Fix for 100vh on mobile browsers
  const setVH = () => {
    const vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
  };

  // Initial set
  setVH();

  // Update on resize and orientation change
  window.addEventListener('resize', () => {
    setTimeout(setVH, 100); // Add delay to ensure correct height after orientation change
  });
  window.addEventListener('orientationchange', () => {
    setTimeout(setVH, 100); // Add delay to ensure correct height after orientation change
  });

  // Detect touch device
  const isTouchDevice = () => {
    return ('ontouchstart' in window) || (navigator.maxTouchPoints > 0);
  };

  if (isTouchDevice()) {
    document.body.classList.add('touch-device');
  }

  // Clean up
  return () => {
    window.removeEventListener('resize', setVH);
    window.removeEventListener('orientationchange', setVH);
  };
};

function App() {
  // Get the base path for routing
  const basePath = getBasePath();
  
  useEffect(() => {
    // Initialize image optimizations
    initImageOptimizations();
    
    // Set up mobile viewport handling
    const cleanup = handleMobileViewport();
    
    // Add smooth scrolling for anchor links
    document.querySelectorAll('a[href^="#"]').forEach(anchor => {
      anchor.addEventListener('click', function (e) {
        e.preventDefault();
        const targetId = this.getAttribute('href');
        const targetElement = document.querySelector(targetId);
        
        if (targetElement) {
          targetElement.scrollIntoView({
            behavior: 'smooth'
          });
        }
      });
    });
    
    return cleanup;
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyles />
      <Router basename={basePath}>
        <Header />
        <ScrollToTop />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/video-editing" element={<VideoEditing />} />
          <Route path="/designing" element={<Designing />} />
          <Route path="/website-support" element={<WebSupport />} />
          <Route path="/creator-outreach" element={<CreatorOutreach />} />
          <Route path="/careers" element={<Careers />} />
          <Route path="/privacy-policy" element={<PrivacyPolicy />} />
          <Route path="/terms-of-service" element={<TermsOfService />} />
        </Routes>
        <Footer />
      </Router>
    </ThemeProvider>
  );
}

export default App;
